/* This stylesheet generated by Transfonter (https://transfonter.org) on March 11, 2018 8:27 PM */

$red: #DC0031;

@font-face {
    font-family: 'Univers LT Std';
    src: url('../fonts/UniversLTStd.woff2') format('woff2'),
        url('../fonts/UniversLTStd.woff') format('woff'),
        url('../fonts/UniversLTStd.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Univers LT Std';
    src: url('../fonts/UniversLTStd-BlackEx.woff2') format('woff2'),
        url('../fonts/UniversLTStd-BlackEx.woff') format('woff'),
        url('../fonts/UniversLTStd-BlackEx.ttf') format('truetype');
    font-weight: 900;
    font-style: normal;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Univers LT Std', 'Helvetica', sans-serif;
  background: #F3F3F3;
  font-size: 16px;
}

.page-content {
  display: flex;
  flex-direction: column;
}

header {
  margin: auto;
  padding: 20px;
}

.mag {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: auto;
  margin: auto;
  max-height: 80vh;
  .mag-cover,
  .mag-details {
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    padding: 4%;
    width: 50%;
    
    min-width: 300px;
  }

  .mag-details {
    display: flex;
    flex-flow: column;

    .elements {
      margin: auto;
    }
  }

  .mag-cover {
    .cover {
      height: 100%;
      img {
        max-width: 100%;
        max-height: 100%;
        width: auto;
        height: auto;
      }
    }
    
  }
}

h1 {
  display: block;
  width: 112px;
  height: 76px;
  font-size: 0;
  margin: auto;
  background: url('/logo.svg') no-repeat center;
  background-size: 100% auto;
}

h2 {
  font-size: 16px;
  text-transform: uppercase;
}

form {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
}

#mc_embed_signup_scroll {
  width: 100%;
}

#mc_embed_signup div.mce_inline_error {
  background: none !important;
  color: $red !important;
  font-weight: normal !important;
}

#mce-success-response {
  padding: 0.5em;
}

input {
  box-sizing: border-box;
  font-family: 'Univers LT Std', 'Helvetica', sans-serif;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  border: 0;
  padding: 0 1.25em;
  margin: 0.5em 0;
  text-align: center;
  outline: none;

  &:focus {
    outline: none;
  }

  &[type="text"],
  &[type="email"] {
    width: 100%;
  }

  &[type="submit"] {
    cursor: pointer;
    background: black;
    color: white;
  }
}